<template>
    <view>

        <view style="
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index:-1;
        background-color: #F5F5F5;
        "></view>

        <view :style="'height: ' + navBarHeight + 'px;'"></view>

        <view style="
        position: fixed;
        z-index:99;width: 100%;background-color: #fff;padding-top:20rpx" :style="{
            'top': navBarHeight + 'px',
        }" id="thatHeader">
            <view class="scroll-list" :indicator="false">
                <!-- recommend -->
                <view class="u-scroll-list-item">
                    推荐
                </view>
                <view v-for="(item, index) in goodsTypeList" class="u-scroll-list-item" :key="index" :style="{
                    'color': item.code == requestParam.typeCode ? '#000' : '#333',
                    'fontWeight': item.code == requestParam.typeCode ? 'bold' : 'none'
                }"
                @click="switchTypeCode(item.code)"
                >
                    {{ item.name }}
                </view>
            </view>
        </view>

        <view :style="'height:' + segmentHeight + 'px'"></view>

        <scroll-view scroll-y :style="'height: calc(100vh - ' + navBarHeight + 'px;)'">
            <view style="height: 20rpx;"></view>

            <view style="
        box-sizing: border-box;
        width: 100%;
        position: relative;
        ">

                <shopGoods v-for="(item, index) in bodyData.content" :item="item" :key="index"></shopGoods>

            </view>

            <view style="height: 200rpx;"></view>
        </scroll-view>
    </view>
</template>


<script>
import shopGoods from './inPageExchange/shopGoods.vue';

export default {
    components: {
        shopGoods
    },

    props: {
        navBarHeight: {
            type: Number,
            default: 0
        },
    },
    data() {
        return {
            segmentHeight: 0,
            goodsTypeList: [],
            bodyData: {
                content: []
            },
            requestParam: {
                pageNumber: 1,
                typeCode: 'propOfCommon'
            },
            isEnd: false
        }
    },
    created() {
        this.getGoodsTypeList();
        this.getByTypeCode();
    },
    mounted() {
        // 获取thatHeader的高度
        this.$nextTick(() => {
            uni.createSelectorQuery().in(this).select('#thatHeader').boundingClientRect((data) => {
                this.segmentHeight = data.height;
            }).exec();
        });

    },
    methods: {
        switchTypeCode(typeCode) {
            this.requestParam.typeCode = typeCode;
            this.requestParam.pageNumber = 1;
            this.bodyData.content = [];
            this.isEnd = false;
            this.getByTypeCode();
        },
        getGoodsTypeList() {
            this.$kt.request.request({
                api: this.$api.business.ovoShop.goodsType.getList,
                stateSuccess: (res) => {
                    this.goodsTypeList = res.data;
                    // 获取thatHeader的高度
                    this.$nextTick(() => {
                        uni.createSelectorQuery().in(this).select('#thatHeader').boundingClientRect((data) => {
                            this.segmentHeight = data.height;
                        }).exec();
                    });
                }
            });
        },
        getByTypeCode() {
            if (this.isEnd) {
                return null;
            }

            this.$kt.request.request({
                api: this.$api.business.ovoShop.goods.getByTypeCode,
                data: {
                    ...this.requestParam
                },
                stateSuccess: (res) => {

                    for (let i = 0; i < res.data.content.length; i++) {
                        this.bodyData.content.push(res.data.content[i]);
                    }

                    if (res.data.content.length < res.data.size) {
                        this.isEnd = true;
                        return null;
                    }

                    this.requestParam.pageNumber++;

                }
            });
        }
    },
}

</script>

<style lang="scss" scoped>
.scroll-list {
    display: flex;
    padding: 20rpx 10rpx 20rpx 10rpx;
    overflow: hidden;
    overflow-x: scroll;
    // 始终不显示滚动条
    -ms-overflow-style: none;

    // 滚动条隐藏
    // ::-webkit-scrollbar {
    //     display: none;
    //     width: 0 !important;
    //     height: 0 !important;
    // }

}

.scroll-list::-webkit-scrollbar {
  width: 0.1rem; /* 设置滚动条宽度 */
  height: 0.1rem; /* 设置滚动条高度 */
}

.scroll-list::-webkit-scrollbar-thumb {
  background-color: transparent; /* 设置滚动条的背景颜色为透明 */
}

.u-scroll-list-item {
    display: inline-block;
    padding: 0rpx 20rpx;
    font-size: 30rpx;
    color: #666;
    // 文字间距
    letter-spacing: 2rpx;
    // 文字不换行
    white-space: nowrap;
}
</style>